
import React from 'react';
import styled from "styled-components";
import {Blue, Black, Gray} from "../utilities";

const AnchorButton = styled.a`
    
    color: #fff;
    display: block;
    background: ${Blue};
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 24px;
    border-radius: 6px;
    box-shadow: none;
    transition: background .5s ease;
    cursor: pointer;
    min-width: 112px;
    width: 112px;
    white-space: nowrap;
    font-family: neue-haas-grotesk-display, sans-serif;
    font-size: 16px;
    &:hover {
        background: #1676C4;
        transition: all .5s ease;
        box-shadow: inset 0 0 10px rgba(100,100,100,.4);
    }

`;

const Button = styled.button`
    color: #fff;
    background: ${Blue};
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 16px;
    border-radius: 6px;
    transition: background .5s ease;
    box-shadow: none;
    border: 0;
    cursor: pointer;
    font-family: neue-haas-grotesk-display, sans-serif;
    font-size: 16px;
    width: 112px;
    white-space: nowrap;

    &:hover {
        background: #1676C4;
        transition: background .5s ease;
        box-shadow: inset 0 0 10px rgba(100,100,100,.4);
    }

`;

export const LinkButton = ({href, target, children, className}) => (
    <AnchorButton className={className} href={href} target={target}>
        {children}
    </AnchorButton>
);


export const NormalButton = ({children}) => (
    <Button>
        {children}
    </Button>
);